'use client';

import React, { useState } from 'react';
import { Box, useMediaQuery, useTheme, Toolbar } from '@mui/material';
import Sidebar from '@/app/dashboard/Sidebar';
import AppToolbar from '@/app/dashboard/AppToolbar';

export default function Template({ children }: { children: React.ReactNode }) {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const [openDrawer, setOpenDrawer] = useState(!isMobile);

  const handleDrawerToggle = () => {
    setOpenDrawer(prev => !prev);
  };

  return (
    <Box sx={{ display: 'flex', minHeight: '100vh', backgroundColor: '#0A0F19', color: 'white' }}>
      {/* Sidebar */}
      <Sidebar openDrawer={openDrawer} handleToggleDrawer={handleDrawerToggle} />

      {/* Main Content */}
      <Box component="main" sx={{ flexGrow: 1, width: '100%' }}>
        <AppToolbar handleDrawerToggle={handleDrawerToggle} />
        {/* spacer for fixed AppBar */}
        <Toolbar />
        <Box sx={{ p: 2 }}>
          {children}
        </Box>
      </Box>
    </Box>
  );
}
